import React from "react";
import SalesAgentUI from "./components/Paywall";
import { checkUsage, trackUsage } from "./lib/paywall";

export default async function SalesAgentPage() {

  // ðŸ”¥ FORCE-UNLOCK FOR YOUR ACCOUNT
  const adminEmail = "solobotagency@gmail.com";
  const { currentUser } = await import("@clerk/nextjs/server");
  const user = await currentUser();
  const email = user?.emailAddresses?.[0]?.emailAddress;

  if (email === adminEmail) {
    return (
      <div className="w-full h-full">
        <SalesAgentUI />
      </div>
    );
  }

  // Normal paywall
  const usage = await checkUsage();

  if (usage.isPremium) {
    return (
      <div className="w-full h-full">
        <SalesAgentUI />
      </div>
    );
  }

  if (usage.isLocked) {
    return (
      <div className="w-full h-full flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-xl font-semibold mb-2">Free Limit Reached</h2>
          <p className="text-sm text-gray-400 mb-4">
            Youâ€™ve used all your free requests for the Sales Agent.
          </p>
          <a
            href="/dashboard/billing"
            className="px-6 py-3 bg-green-500 rounded-lg text-white font-semibold"
          >
            Upgrade to Unlock Unlimited Access
          </a>
        </div>
      </div>
    );
  }

  await trackUsage();

  return (
    <div className="w-full h-full">
      <SalesAgentUI />
    </div>
  );
}
